const initialState = {
  voice:0,
  showVoice: false,
};

// action types
export const types = {
  CHANGE_VOICE:'VOICE/CHANGE_VOICE',
  CHANGE_VOICE_SHOW:'VOICE/CHANGE_VOICE_SHOW',
};

// action creators
export const actions = {
  changeVoice: (data) => ({
    type: types.CHANGE_VOICE,
    data
  }),
  changeVoiceShow: (data) => ({
    type: types.CHANGE_VOICE_SHOW,
    data
  })
};

// reducers
const reducer = (state = initialState, action) => {
  switch (action.type) {
    case types.CHANGE_VOICE:
      return {
        ...state,
        voice: action.data,
        showVoice: true,
      }
    case types.CHANGE_VOICE_SHOW:
      return {
        ...state,
        showVoice: action.data
      }
    default:
      return state;
  }
};

export default reducer;
