import React, { Component } from 'react';
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {getStep, actions} from '../../redux/modules/game';

import './style.scss';

class GameOptionTitle extends Component {
  getOptions = (step) => {
    let data = this.props.options;
    switch(step) {
      case 0:
        return {
          classes: 'step-1 chess-layout-flex',
          title: '棋类'
        }
      case 1:
        return {
          classes: 'step-2 chess-layout-flex',
          title: `${data[0]['name']}▲角色`
        }
      case 2:
        return {
          classes: 'step-3 chess-layout-flex',
          title: `${data[0]['name']}▲${data[1]['name']}▲棋力`
        }
      default: 
        return {
          classes: 'step-4 chess-layout-flex',
          title: `${data[0]['name']}▲${data[1]['name']}▲${data[2]['name']}`
        }
    }
  }

  render() {
    let {steps} = this.props;
    let {title} = this.getOptions(steps);
    let titleList = title.split('▲');
    titleList = titleList.map((item, index, self) => {
      
      return <div key={`chess-title-item${index}`}>
        <div 
        className="chess-title-item">
        {item}
        </div>
        {index < self.length - 1 ? <div className="chess-title-arrow">{'▲'}</div> : ''}
      </div>
    })
    return(
      <div>
         <h1 className="chess-title">
           {titleList}
        </h1>
      </div>
    )
  }
}
const mapStateToProps = (state, props) => {
  return {
    steps: getStep(state), 
    options:state.game.options,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    ...bindActionCreators(actions, dispatch),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(GameOptionTitle)