import React, { Component } from 'react';
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
//import Konva from 'konva';
import { Stage, Layer, Line, Image, Circle, Text, Shape} from 'react-konva';
import WaringForChess from '../../components/WarningForChess';
import CountDown from '../../components/CountDown';
import DiaLog from '../../components/DiaLog';
import {actions} from '../../redux/modules/chess';
import {actions as dialogActions} from '../../redux/modules/dialog';

import './style.scss';
import imgBg from '../../static/img/gmk/gmk_play/vboard.png';
//倒计时图片
// import imgC1 from '../../static/img/gmk/gmk_countdown/c1.png';
// import imgC2 from '../../static/img/gmk/gmk_countdown/c2.png';
// import imgC3 from '../../static/img/gmk/gmk_countdown/c3.png';
// import imgC4 from '../../static/img/gmk/gmk_countdown/c4.png';
class GamePlay extends Component {
  state = {
    //游戏开始
    gameWatting:true,
    // 背景图
    imgBg:null,
    imgBgWidth: 823,
    imgBgHeight: 840,
    interval: 36, //格子规格
    topMargin: 96, //边距
    leftMargin: 87,
  }
  // _showWarningForChess(x,y){
  //   let cx,cy;
  //   cx = x * this.state.interval + this.state.leftMargin;
  //   cy = y * this.state.interval + this.state.topMargin;
  //   this.setState({
  //     //showWarningForChess: !this.state.showWarningForChess,
  //     axisForWarn:[cx, cy]
  //   })
  // }
  handleClick = (e) => {
    if (process.env.NODE_ENV === 'production') {
      return;
    }
    let x = e.evt.offsetX;
    let y = e.evt.offsetY;
    let state = this.state;
    if (x > 835 || x < state.leftMargin || y > 853 || y < state.topMargin) {
      return;
    }
    x = Math.round((x - state.leftMargin) / state.interval);
    y = Math.round((y - state.topMargin) / state.interval);
    this.props.addChess(this.props.who, [x,y]);
    
    //test for show warning
    // this.props.showDialog({
    //   bol: true,
    //   dialogHeader: '发现有遮挡物',
    //   dialogContent1: '发现有遮挡物物发现有遮挡',
    //   dialogContent2: '发现有遮挡物发现有遮挡',
    // })
    // var img = new window.Image();
    // img.src ='http://192.168.1.82:8080/aigoface/aigo/img_error.png'
    // img.onload = () => {
    //   this.props.showWarnig({
    //     showWarningForChess: true,
    //     axisForWarn:[6, 7, 7, 12, 7, 13, 7, 14, 7, 15, 7, 16, 7, 17, 7, 18, 8, 10, 8, 12, 8, 13, 8, 14, 8, 15, 8, 16, 8, 17, 8, 18, 9, 7, 9, 8, 9, 9, 9, 10, 9, 11, 9, 13, 9, 15, 9, 16, 9, 17, 9, 18, 10, 8, 10, 12, 10, 13, 10, 14, 10, 15, 11, 6, 11, 11, 12, 11],
    //     axisType:2,
    //     imageData: {
    //       img,
    //       dh: 480,
    //       dw: 480,
    //       gh: 21.818182,
    //       gw: 21.818182,
    //       mark: "cover",
    //       msg: "board",
    //       stone:[6, 7, 7, 12, 7, 13, 7, 14, 7, 15, 7, 16, 7, 17, 7, 18, 8, 10, 8, 12, 8, 13, 8, 14, 8, 15, 8, 16, 8, 17, 8, 18, 9, 7, 9, 8, 9, 9, 9, 10, 9, 11, 9, 13, 9, 15, 9, 16, 9, 17, 9, 18, 10, 8, 10, 12, 10, 13, 10, 14, 10, 15, 11, 6, 11, 11, 12, 11],
    //       x0: 43,
    //       y0: 43,
    //     }
    //   })
    // }
  }
  handleKeydonwn (e){
    console.log(e)
  }
  //倒计时结束
  childAnimationEnd = () => {
    this.setState({
      gameWatting: false
    })
  }
  //计算坐标
  computedAxies = (x, y) => {
    let dx ,dy;
    let state = this.state;
    //根据坐标求出偏移量
    dx = x * state.interval + state.leftMargin;
    dy = y * state.interval + state.topMargin;
    return [dx, dy];
  }
  // 渲染
  handleShow = () => {
    let shamps = [];
    let newPoints;
    let blackArray = this.props.black;
    let whitekArray = this.props.white;
    let chessPointer = this.props.chessPointer;
    let keyString;
    // 是否有新增点
    if (chessPointer.length > 0 ) {
      let [px, py] = this.computedAxies(chessPointer[0], chessPointer[1]);
      keyString = [chessPointer[0], chessPointer[1]].toString();
      //let color = chessPointer[2];
      //console.log(keyString)
      newPoints =
        <Shape
          key={`${px}${py}`}
          sceneFunc={(context, shape) => {
            context.beginPath();
            context.moveTo(px, py);
            context.lineTo(px + 15, py);
            context.lineTo(px, py + 15);
            context.lineTo(px, py);
            context.closePath();
            context.fillStrokeShape(shape);
          }}
          fill={'red'}
        />
    }
    blackArray.forEach((item, key, self) => {
      let [x,y] = this.computedAxies(item.dx, item.dy);
      let px = 15 * item.step.toString().length / 4;
      shamps.push(
        <Circle 
          key={item.id}
          x={x} 
          y={y}
          radius={15} 
          fill= {item.color}
        />)
        
      if (key !== keyString) {
        shamps.push(
          <Text
            key={`${item.id}-text`}
            x={x} 
            y={y}
            text={item.step}
            fill="white"
            offsetX={px}
            offsetY={7.5}
            fontSize={15}
          />)
      } 
    })
    whitekArray.forEach((item, key, self) => {
      let [x,y] = this.computedAxies(item.dx, item.dy);
      let px = 15 * item.step.toString().length / 4;
      shamps.push(
        <Circle 
        key={item.id}
        x={x} 
        y={y}
        radius={15} 
        fill= {item.color}
      />)
      if (key !== keyString) {
        shamps.push(
          <Text
            key={`${item.id}-text`}
            x={x} 
            y={y}
            text={item.step}
            fill="black"
            offsetX={px}
            offsetY={7.5}
            fontSize={15}
          />
          );
      }   
    })
    shamps.push(newPoints);
    return shamps;
  }
  componentDidMount() {
    //window.addEventListener('keydown',this.handleKeydonwn,false);
    const image = new window.Image();
    image.src = imgBg;
    image.onload = () => {
      this.setState({
        imgBg: image
      });
    };
  }
  render() {
    
    let warningArray = [];
    let shamps = this.handleShow();
    // 是否显示提示线
    if (this.props.showWarningForChess) {
      let axisArray = [...this.props.axisForWarn];
      for (let i = 0; i < axisArray.length; i+=2) {
        warningArray.push(...this.computedAxies(axisArray[i], axisArray[i+1]));
      }
    }
    
    return(
      
        this.state.gameWatting ? <CountDown
        animationEnd = {this.childAnimationEnd}
        />:
        <div className="canvas-wrap">
          <Stage width={this.state.imgBgWidth} height={this.state.imgBgHeight}
          onClick = {this.handleClick}
          >
            <Layer>
              <Image
                image={this.state.imgBg}
                width={this.state.imgBgWidth}
                height={this.state.imgBgHeight}
              />
            </Layer>
            <Layer>
            {
            shamps
            }
            </Layer>
            {
              this.props.showWarningForChess &&
              <WaringForChess
              axisForWarn={warningArray}
              axisType={this.props.axisType}
              imageData = {this.props.imageData}
              />
            }
            
        </Stage>
        <div> 
        {
          this.props.dialog && <DiaLog
            type = {this.props.dialogType}
            title = {this.props.dialogTitle}
            header={this.props.dialogHeader}
            content1={this.props.dialogContent1}
            content2={this.props.dialogContent2}
          />
        }
        </div>
      </div>  
    )
  }
}

const mapStateToProps = (state, props) => {
  return {
    white: state.chess.white,
    black: state.chess.black,
    who: state.chess.whoIsPlaying,
    chessPointer: state.chess.chessPointer,
    dialogType:state.dialog.dialogType,
    dialogTitle: state.dialog.dialogTitle,
    dialog: state.dialog.dialog,
    dialogHeader: state.dialog.dialogHeader,
    dialogContent1: state.dialog.dialogContent1,
    dialogContent2: state.dialog.dialogContent2,
    showWarningForChess: state.chess.showWarningForChess,
    axisType:state.chess.axisType,
    axisForWarn: state.chess.axisForWarn,
    imageData: state.chess.imageData
  };
};

const mapDispatchToProps = dispatch => {
  return {
    ...bindActionCreators(actions, dispatch),
    ...bindActionCreators(dialogActions, dispatch),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(GamePlay);