import React from 'react';
import './style.scss';
//按钮
import imgBtnConfirm from '../../static/img/button/btn_confirm.png';
import imgWinner0 from '../../static/img/chess/winner_0.png';
import imgWinner1 from '../../static/img/chess/winner_1.png';
import imgWinner2 from '../../static/img/chess/winner_2.png';
import hp0 from '../../static/img/chess/hp_0.png';
import hp1 from '../../static/img/chess/hp_1.png';
import chess0 from '../../static/img/countdown/black.png';
import chess1 from '../../static/img/countdown/white.png';
export default function DiaLog(props) {
  
  const dialog = <div className={props.header ? 'dialog-main' : 'dialog-main dialog-center'}>
    <p className="dialog-header" style={{display: props.header ? 'block' : 'none'}}>
      {props.header}
    </p>
    <p className="dialog-content">
      {props.content1}
      {props.content1 && props.content2 ? <img
          className="dialog-btn"
          src={imgBtnConfirm} alt="" /> : ''}
      {props.content2}
    </p>
  </div>;
  let srcInformation, srcChessType;
  let classNameTop, classNameBottom;
  switch (props.title) {
    case 0:
      srcInformation = imgWinner0;
      classNameBottom = 'dialog-winner';
      break;
    case 1:
      srcInformation = imgWinner1;
      classNameBottom = 'dialog-winner';
      break;
    case 2:
      srcInformation = imgWinner2;
      classNameBottom = 'dialog-winner';
      break;
    case 3:
      srcInformation = hp0;
      srcChessType = chess0;
      classNameTop = 'dialog-chess';
      classNameBottom = 'dialog-who-play';
      break;
    case 4:
      srcInformation = hp0;
      srcChessType = chess1;
      classNameTop = 'dialog-chess';
      classNameBottom = 'dialog-who-play';
      break;
    case 5:
      srcInformation = hp1;
      srcChessType = chess0;
      classNameTop = 'dialog-chess';
      classNameBottom = 'dialog-who-play';
      break;
    case 6:
      srcInformation = hp1;
      srcChessType = chess1;
      classNameTop = 'dialog-chess';
      classNameBottom = 'dialog-who-play';
      break;
    default:
      break;
  }
  const title = <div className="dialog-title">
    <div className = {classNameTop}>
      <img src={srcChessType} alt=""/>
    </div>
    <div className = {classNameBottom}>
      <img src={srcInformation} alt=""/>
    </div>
  </div>;

  return (
    <div>
      {
        props.type === 1 ? dialog : title
        //dialog
      }
    </div>
  )
}