import React, { PureComponent } from 'react';
import Konva from 'konva';
import { Layer, Line, Circle, Image} from 'react-konva';

class WaringForChess extends PureComponent {
  state = {
    anim: null,
    hideLines:false,
  }
  componentDidMount = () => {
    this.props.axisType < 2 && this.animate1();
  }
  componentWillUnmount = () => {
    this.state.anim && this.state.anim.stop()
  }
  animate1 = () => {
    
    let state = 0;
    let times = 0
    var anim = new Konva.Animation((frame) => {
          this.circles.forEach((item, index) => {
            if (state === 0) {
              if (item.attrs.radius - frame.timeDiff / 2 <= 15) {
                item.attrs.radius = 15;
                state++;
                
              } else {
                item.attrs.radius -= frame.timeDiff / 2;
              }
            } else if (state === 1) {
              item.attrs.radius = 15;
            }
            else {
              let data = 10 * Math.sin(times * 2 * Math.PI / 2000) + 25;
              item.attrs.radius = data;
            }
          })
          if (state === 1) {
            state++;
            this.setState({
              hideLines: true
            })
          } else if (state === 2) {
            times+= frame.timeDiff;
          }
        }, this.refs.layer);
    this.setState({
      anim
    })
    anim.start();
  }
  handleErrorAxis = () => {
    let axisArray = [...this.props.axisForWarn];
    if (axisArray.length % 2 !== 0) {
      axisArray.pop()
    }
    let shamps = [];
    let lines = [];
    this.circles = []
    for (let i = 0; i < axisArray.length; i += 2) {
      let x = axisArray[i];
      let y = axisArray[i + 1];
      shamps.push(
        <Circle
          key={`WaringForChess-circle${i}`}
          ref={node => {
            if (node) {
              this.circles.push(node)
            }
            return node
          }}
          x={x}
          y={y}
          radius={400}
          stroke="red"
          strokeWidth={4}
          onFinish = {this.finish}
        />
      )
      if (this.props.axisType === 0) {
        lines.push(
          <Line
            key={`WaringForChess-lines${i}`}
            points={[
              x - 1000, y,
              x + 1000, y,
              x, y,
              x, y + 1000,
              x, y - 1000
            ]}
            stroke="red"
          />
        )
      }
    }
    return [shamps, lines];
  }
  handleErrorImage = (res) => {
    let shamps =[];
    
    let img = res.img;
    let imgaeData = res.data;
    let width = img.width;
    let height = img.height;
    let x = imgaeData.x0 / imgaeData.dw * width;
    let y = imgaeData.y0 / imgaeData.dh * height;
    let dw = imgaeData.gw / imgaeData.dw * width;
    let dh = imgaeData.gh / imgaeData.dh * height;
    let cropWidh = dw * 18;
    let cropHeight = dh * 18;
    let dw_ = 823 / 18;
    let dh_ = 840 / 18;
    shamps.push(<Image
      key="errorImage"
      image={img}
      width={823}
      height={840}
      crop ={{
        x,
        y,
        width: cropWidh,
        height: cropHeight
      }}
    />)
    if (imgaeData.mark === 'cover') {
      let points = imgaeData.stone;
      for(let i = 0; i < points.length; i += 2) {
        let x = points[i] * dw_;
        let y = points[i + 1] * dh_;
        shamps.push(
          <Circle
          key={`errorImage-circle${i}`}
          x={x}
          y={y}
          radius={6}
          fill="red"
        />
        )
      }
    }
    return shamps;
  }
  render() {
    let shamps
    if (this.props.axisType < 2) {
      let shampsArray = this.handleErrorAxis();
      if (this.state.hideLines) {
        shamps = shampsArray[0]
      } else {
        shamps = [...shampsArray[0], ...shampsArray[1]]
      }
    } else if (this.props.axisType === 2) {
      shamps = this.handleErrorImage(this.props.imageData)
    }
    
    
    return (
      <Layer ref="layer">
        {shamps}
      </Layer>
    );
  }
}

export default WaringForChess;