import React, {Component} from 'react';
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {Route,  withRouter} from "react-router-dom";
import Menu from '../Menu';
import Game from '../Game';
import Setting from '../Setting';
import Portal from '../../components/Portal';
import Voice from '../../components/Voice';
import createWS from '../../common/websocket';
import {actions} from '../../redux/modules/app';
import {actions as gameActions, getList, getStep} from '../../redux/modules/game';
import {actions as chessActions} from '../../redux/modules/chess';
import {actions as dialogActions} from '../../redux/modules/dialog';
import {actions as settingActions} from '../../redux/modules/setting';

import './reset.scss';
import './style.scss';
import './globalfont.css';
//import logoFullImg from '../../static/img/logo/logo_full.png';
//import logoImg from '../../static/img/logo/logo.png';

class App extends Component{
  state = {
    socketUrl : process.env.NODE_ENV === 'development' ?
    'ws://192.168.1.82:8080/aigoface/websocket' : 'ws://127.0.0.1:8080/aigoface/websocket',
  }
  
  componentDidMount = () => {
    
    //new createWS("ws://127.0.0.1:8001", this);
    let sock = new createWS(this.state.socketUrl, this);
    
    window.addEventListener("beforeunload", function (e) {
      sock.ws.close();
    });
  }
 
  
  render() {
    //let titleImg;
    //this.props.ifTitleImgFull ? titleImg = logoFullImg : titleImg = logoImg;
    //console.log(this.props.history.location.pathname)
    return <div className="bg">
        <div className="content">
          <Route exact path="/" component={Menu} />
          <Route path="/game" component={Game}/>
          <Route path="/setting" component={Setting}/>
        </div>
        {
          this.props.showVoice && <Portal>
          <Voice
            voice = {this.props.voice}
          />
        </Portal>
        }
    </div>
  }
}

const mapStateToProps = (state, props) => {
  return {
    ifInstruction: state.app.ifInstruction,
    process: state.app.process,
    mode: state.app.mode,
    steps: getStep(state), 
    optionList: getList(state),
    options: state.app.options,
    ifDialog: state.dialog.dialog,
    ifWarning: state.chess.showWarningForChess,
    voice:state.setting.voice,
    showVoice: state.setting.showVoice
  };
};

const mapDispatchToProps = dispatch => {
  return {
    ...bindActionCreators(actions, dispatch),
    ...bindActionCreators(gameActions, dispatch),
    ...bindActionCreators(chessActions, dispatch),
    ...bindActionCreators(dialogActions, dispatch),
    ...bindActionCreators(settingActions, dispatch),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));