const initialState = {
  white: new Map(),
  black: new Map(),
  chessPointer: [],//最新子落点及颜色
  whoIsPlaying: 0, //当前将要下棋角色
  showWarningForChess: false,//棋子位置提示
  axisForWarn:[],//提示坐标
  axisType:0,//提示类型
  imageData:null,//遮挡图片
};

// action types
export const types = {
  ADD_CHESS: "CHESS/ADD_CHESS",   // 点击添加棋子
  TRANSFORM_CHESS: "CHESS/TRANSFORM_CHESS", // 转变返回棋子数据结构
  RESET_CHESS: "CHESS/RESET_CHESS",
  SHOW_WARNING: "CHESS/SHOW_WARNING",

};

// action creators
export const actions = {
  //添加棋子，目前只用于点击
  addChess: (chessType, axis) => ({
    type: types.ADD_CHESS,
    chessType,
    axis
  }),
  //
  transform: (data) => ({
    type: types.TRANSFORM_CHESS,
    data
  }),
  resetForChess: () => ({
    type: types.RESET_CHESS,
  }),
  showWarnig: (data) => ({
    type: types.SHOW_WARNING,
    data
  })
};

// reducers
const reducer = (state = initialState, action) => {
  switch (action.type) {
    case types.ADD_CHESS:
      let map_add, color_add, color_point, who, step;
      if (action.chessType === 0) {
        map_add = state.black;
        step = map_add.size * 2 + 1;
        color_add = 'black';
        color_point = 'white';
        who = 1;
      } else {
        map_add = state.white;
        step = map_add.size * 2 + 2;
        color_add = 'white';
        color_point = 'black'
        who = 0;
      }
      let [x_add, y_add] = action.axis;
      map_add.set(`${x_add},${y_add}`, {
        id: `${x_add},${y_add}`,
        dx: x_add,
        dy: y_add,
        color: color_add,
        step
      })
      return { 
        ...state,
        whoIsPlaying: who,
        chessPointer:[x_add, y_add, color_point],
        [color_add]: new Map(map_add)};
    case types.TRANSFORM_CHESS:
      let mapb = new Map();
      let mapw = new Map();
      let chessArrayBlack = action.data.black;
      let black_i = action.data.black_i;
      let chessArrayWhite = action.data.white;
      let white_i = action.data.white_i;
      let chessNew = [];
      if (action.data.newPoiner) {
        chessNew = [...action.data.newPoiner];
        chessNew[2] === 0 ? chessNew[2]='white' : chessNew[2]='black';
      } 
      //console.log(chessArrayBlack,chessArrayWhite);
      for (let x = 0, y = 0; x < chessArrayBlack.length; x += 2,y++) {
        let [dx, dy] = [chessArrayBlack[x], chessArrayBlack[x+1]];
        mapb.set(`${dx},${dy}`, {
          id: `${dx},${dy}`,
          dx,
          dy,
          color: 'black',
          step: black_i[y] + 1
        })
      }
      for (let x = 0, y = 0; x < chessArrayWhite.length; x += 2, y++) {
        let [dx, dy] = [chessArrayWhite[x], chessArrayWhite[x+1]];
        mapw.set(`${dx},${dy}`, {
          id: `${dx},${dy}`,
          dx,
          dy,
          color: 'white',
          step: white_i[y] + 1
        })
      }
      
      return { 
        ...state,
        black: mapb, 
        white: mapw,
        chessPointer: [...chessNew]
      };
    case types.RESET_CHESS:
      return {
        white: new Map(),
        black: new Map(),
        chessPointer: [],
        whoIsPlaying: 0,
        showWarningForChess: false,
        axisForWarn:[],
        axisType:0,
        imageData:null,
      }
    case types.SHOW_WARNING:
      return{
        ...state,
        showWarningForChess: action.data.showWarningForChess,
        axisForWarn: action.data.axisForWarn,//提示坐标
        axisType: action.data.axisType, //提示类型 0错误 1五子棋五练2棋盘遮挡
        imageData: action.data.imageData
      }
    default:
      return state;
  }
};

export default reducer;


