import React from 'react'
import './style.scss';

export default function CountDown(props) {
  function animationend(e) {
    if (e.animationName === 'number') {
      window.removeEventListener('animationend', animationend);
      props.animationEnd();
    }
  }
  window.addEventListener('animationend', animationend);
  
  return (
    <div className="count-wrap">
      <div className="count-chess1"></div>
      <div className="count-chess2"></div>
      <div className="count-number"></div>
      <div className="count-bg1"></div>
      <div className="count-bg2"></div>
      <div className="count-bg3"></div>
    </div>
  )
}