class createWS {
  constructor(url, props) {
    this.ws = new WebSocket(url);
    this.url = url;
    this.origin = props;
    //idle 第一次为程序开始其余为错误 不重置
    this.idle = 0;
    //每项选项
    this.chooseId = 0;
    //是否设置棋类
    this.chess = null;
    //选中棋类
    this.chessType = null;
    //下棋过程的功能键状态
    this.showKbType = '';
    // 游戏状态 暂停、提示、退出
    this.gameStates = null;
    // 当前提示次数
    this.helpTime = 0;
    //当前游戏角色(弹框部分)
    this.player = null;
    // 全局遮挡图片
    this.errorImage = null;
    //维持链接
    this.timer = null;
    //载入
    this.initEvent();
  }
  //发送消息
  sendMessage(ws, data) {
    console.log(data);
    let data_ = JSON.stringify(data);
    ws.send(data_);
  }
  //维持链接
  initTimer() {
    if (this.timer) {
      clearInterval(this.timer);
    }
    let n = 0;
    let timer = setInterval(() => {
      if (++n >= 8) {
        this.sendMessage(this.ws, '');
        this.initTimer(timer);
      }
    }, 1000)
    this.timer = timer;
  }
  // 重置游戏
  resetGame() {
    console.log('reset')
    //重置redux game chess
    this.origin.props.resetForGame();
    this.origin.props.resetForChess();
    //重置进程
    this.origin.props.swithProcss('instrction');
    // 重置配置项下标
    this.chooseId = 0;
    // 重置棋类
    this.chess = null;
    this.chessType = null;
    // 重置操作版
    this.showKbType = '';
    // 重置暂停状态
    this.gameStates = null;
    // 重置当前提示次数
    this.helpTime = 0;
    // 重置使用说明
    this.origin.props.switchInstrction(true);
    // 重置错误图片
    this.errorImage = null;
    //重置当前角色
    this.player = null;
    // 重置错误提示框
    this.origin.props.showDialog({
      bol: false
    });
    // 重置路由
    this.origin.props.history.replace('/');
    // 通知机器返回主界面
    this.sendMessage(this.ws, {
      cmd: 'page',
      page: 'home',
      touch: 'kb_home',
    })
  }
  initEvent() {
    this.ws.onopen = () => {
      console.log('websocket连接成功');
      //this.initTimer();
      // 路由不正确时重置
      if (this.origin.props.history.location.pathname !== '/') {
        //this.resetGame();
        this.origin.props.history.replace('/');
        return;
      }
      // 发送home信息表示开始
      // this.sendMessage(this.ws, {
      //   cmd: 'page',
      //   page: 'home'
      // })
    };
    this.ws.onmessage = (e) => {
      let data;
      this.initTimer();
      try {
        data = JSON.parse(e.data);
        if (typeof data !== 'object') {
          console.warn('warn:返回值不是对象类型');
          return
        }
      } catch (error) {
        console.error('error:json解析错误');
        return
      }
      console.log(data, this.origin.props.process);
      //this._errorGlobal(data);
      this._handleGlobal(data);
      switch (this.origin.props.process) {
        case 'instrction':
          this._handleInstrction(data);
          break;
        case 'home':
          this._handleHome(data);
          break;
        case 'game':
          this._handleGame(data);
          break;
        case 'play':
          this._handlePlay(data);
          break;
        case 'endgame':
          this._handleEndGame(data);
          break;
        case 'winner':
          this._handleWinner(data);
          break;
        default:
          break;
      }
    }
    this.ws.onerror = (e) => {
      console.log('error');
    }
    this.ws.onclose = (e) => {
      console.log('closed', e.code);
      if (this.timer) {
        clearInterval(this.timer);
      }
      if (e.code !== 1000 ){
        setTimeout(() => {
          this.ws = new WebSocket(this.url);
          console.log('try again');
          this.initEvent();
        }, 2000);
      }
      
    }

  }
  //全局处理
  _handleGlobal(data) {
    if (data.state === 'idle') {
      if (this.idle > 0) {
        return this.resetGame();
      }
      this.idle++;
      console.log(`${this.idle},次数大于一为异常`);
    }
    if (data.vol) {
      this.origin.props.changeVoice(Number(data.vol));
      setTimeout(() => {
        this.origin.props.changeVoiceShow(false)
      }, 1500)
    }
  }
  // 计算输赢
  _countWin(data) {
    console.log('计算输赢');
    if (data.score == 0) {
      this.origin.props.showTitle({
        bol: true,
        title: 2
      })
    } else if (data.score > 0) {
      this.origin.props.showTitle({
        bol: true,
        title: 1
      })
      this.origin.props.showWarnig({
        showWarningForChess: true,
        axisForWarn: data.stone,
        axisType: 1
      })
    } else if (data.score < 0) {
      this.origin.props.showTitle({
        bol: true,
        title: 0
      })
      this.origin.props.showWarnig({
        showWarningForChess: true,
        axisForWarn: data.stone,
        axisType: 1
      })
    }
    this.origin.props.swithProcss('winner');

  }
  // 遮挡物时弹出图片
  _errorImage(data) {
    let img = new Image();
    let src = process.env.NODE_ENV === 'development' ?
    `http://192.168.1.82:8080/aigoface/aigo/img_error.png?${Math.random()}` : `http://127.0.0.1:8080/aigoface/aigo/img_error.png?${Math.random()}`;
    img.src = src;
    img.onload = () => {
      
      this.origin.props.showWarnig({
        showWarningForChess: true,
        axisForWarn:data.stone,
        axisType:2,
        imageData: {
          img,
          data,
        }
      })
    }
    this.errorImage = img;
  }
  // 异常弹框
  _errorDialog(data) {
    if (data.msg === 'board') {
      switch (data.mark) {
        case 'cover':
          this.origin.props.showDialog({
            bol: true,
            dialogHeader: '发现有遮挡物',
            dialogContent1: '请移开遮挡物后，并点击',
            dialogContent2: '确认键继续',
          })
          this._errorImage(data);
          break;
        case 'noise':
          this.origin.props.showDialog({
            bol: true,
            dialogHeader: '棋子位置不正确',
            dialogContent1: '请移动棋子位置后，并点击',
            dialogContent2: '确认键继续',
          })
          this.origin.props.showWarnig({
            showWarningForChess: true,
            axisForWarn: data.stone,
            axisType:0
          })
          break;
        case 'many':
          this.origin.props.showDialog({
            bol: true,
            dialogHeader: '帮助取子',
            dialogContent1: '请帮助取走多余棋子后，并点击',
            dialogContent2: '确认键继续',
          })
          this.origin.props.showWarnig({
            showWarningForChess: true,
            axisForWarn: data.stone,
            axisType:0
          })
          break;
        case 'tizi':
          this.origin.props.showDialog({
            bol: true,
            dialogHeader: '帮助提子',
            dialogContent1: '请帮助取走多余棋子后，并点击',
            dialogContent2: '确认键继续',
          })
          this.origin.props.showWarnig({
            showWarningForChess: true,
            axisForWarn: data.stone,
            axisType:0
          })
          break;
        case 'clear':
          this.origin.props.showDialog({
            bol: true,
            dialogHeader: '清空棋盘',
            dialogContent1: '请清空棋盘，并点击',
            dialogContent2: '确认键继续',
          })
          this._errorImage(data);
          break;
        case 'dark':
          this.origin.props.showDialog({
            bol: true,
            dialogHeader: '周围光线过暗',
            dialogContent1: '请调整周围环境光线后，并点击',
            dialogContent2: '确认键继续',
          })
          break;
        case 'put':
          this.origin.props.showDialog({
            bol: true,
            dialogHeader: '帮助落子',
            dialogContent1: '请您帮忙在指定位置补一颗棋子后，并点击',
            dialogContent2: '确认键继续',
          })
          this.origin.props.showWarnig({
            showWarningForChess: true,
            axisForWarn: data.stone,
            axisType:0
          })
          break;
        case 'add':
          this.origin.props.showDialog({
            bol: true,
            dialogHeader: '帮旗盒补子',
            dialogContent1: '请在棋盒内放些棋子后，并点击',
            dialogContent2: '确认键继续',
          })
          break;
        default:
          break;
      }
    }
    // 取消异常提示框
    if (data.key === 'ok') {
      if (this.origin.props.ifDialog) {
        this.origin.props.showDialog({
          bol: false
        });
      }
      if (this.origin.props.ifWarning) {
        this.origin.props.showWarnig({
          showWarningForChess: false
        });
      }

    }

  }
  // 设置棋类
  _setChessType(data) {
    //设置棋类
    if (!this.chess && data.chess) {
      let chessArray = data.chess.map((item) => {
        return {
          id: item,
          url: `imgChessType${item}`
        }
      })
      chessArray = chessArray.sort((a,b) => {return b.id - a.id})
      this.origin.props.setChessType(chessArray);
      this.chess = true;
    }
  }
  // 菜单页说明部分 
  _handleInstrction(data) {
    this._setChessType(data);
    
    if (data.key === 'ok') {
      console.log(this.origin.props.ifInstruction);
      if (this.origin.props.ifInstruction === true) {
        this.origin.props.switchInstrction();
        this.origin.props.swithProcss('home');
      } else {
        this.sendMessage(this.ws, {
          cmd: 'page',
          page: 'chess'
        })
      }
    }
  }
  // 菜单页选择部分
  _handleHome(data) {
    this._setChessType(data);
    if (data.key === 'left' || data.key === 'right' || data.key === 'up' || data.key === 'down') {
      this.origin.props.switchMode();
    } else if (data.key === 'ok') {

      if (this.origin.props.mode === 0) {
        this.sendMessage(this.ws, {
          page: {
            page: 'chess'
          }
        })
        this.origin.props.history.replace('/game/options');
        this.origin.props.swithProcss('game');
      } else {
        //this.origin.props.history.replace('/setting');
        //this.origin.props.swithProcss('setting');
      }
    }
  }
  // 游戏页设置部分
  _handleGame(data) {
    this._setChessType(data);
    //console.log(this.origin.props.optionList);
    let maps = this.origin.props.optionList;
    let list = [...this.origin.props.optionList.keys()];
    let len = list.length;
    let steps = this.origin.props.steps;
    if (data.key === 'left') {
      if (this.chooseId <= 0) {
        this.chooseId = len - 1;
      } else {
        this.chooseId = --this.chooseId;
      }
      this.origin.props.switchOptons(list[this.chooseId]);
    } else if (data.key === 'right') {
      if (this.chooseId >= len - 1) {
        this.chooseId = 0;
      } else {
        this.chooseId = ++this.chooseId;
      }
      this.origin.props.switchOptons(list[this.chooseId]);
    } else if(data.key === 'up' && steps === 2) {
      if (this.chooseId === 0) {
        this.chooseId = len - 1;
      } else if (this.chooseId <= len / 2 - 1) {
        this.chooseId += len / 2 - 1;
      } else if (this.chooseId > len / 2 - 1) {
        this.chooseId -= len / 2;
      }
      
      console.log(this.chooseId)
      this.origin.props.switchOptons(list[this.chooseId]);
    } else if(data.key === 'down' && steps === 2) {
      if (this.chooseId === len - 1) {
        this.chooseId = 0;
      } else if (this.chooseId <= len / 2 - 1) {
        this.chooseId += len / 2;
      } else if (this.chooseId > len / 2 - 1) {
        this.chooseId -= len / 2 - 1;
      }
      this.origin.props.switchOptons(list[this.chooseId]);
    } else if (data.key === 'ok') {
      console.log(list, this.chooseId)
      switch (steps) {
        case 0:
          this.origin.props.setOptions('chess', list[this.chooseId]);
          this.chessType = list[this.chooseId];
          this.sendMessage(this.ws, {
            cmd: 'set',
            set: [{ reg: 'chess', value: maps.get(list[this.chooseId]).id}],
            page: {
              page: 'role',
            },

          })
          //重置为首选项
          this.chooseId = 0;
          this.origin.props.switchOptons(list[0]);
          break;
        case 1:
          console.log('role')
          this.origin.props.setOptions('role', list[this.chooseId]);
          this.sendMessage(this.ws, {
            cmd: 'set',
            set: [{ reg: 'role', value: maps.get(list[this.chooseId]).id }],
            page: {
              page: 'level'
            }
          })
          //重置为首选项
          this.chooseId = 0;
          this.origin.props.switchOptons(list[0]);
          break;
        case 2:
          console.log('level')
          this.origin.props.setOptions('level', list[this.chooseId]);
          this.origin.props.swithProcss('play');
          this.sendMessage(this.ws, {
            cmd: 'set',
            set: [{ reg: 'level', value: maps.get(list[this.chooseId]).id }],
          })
          this.origin.props.history.replace('/game/play');
          setTimeout(() => {
            this.sendMessage(this.ws, {
              page: {
                page: 'play',
              },
              touch: 'kb_play'
            })
            this.showKbType = 'kb_play';
          }, 1500)
          //重置为首选项
          this.chooseId = 0;
          this.origin.props.switchOptons(list[0]);
          break;
        default:
          return
      }
      this.origin.props.switchGameStep();
    }
  }
  // 游戏开始部分
  _handlePlay(data) {
    this._errorDialog(data);
    // 根据白黑集合
    if (data.black) {
      this.origin.props.transform({
        black: data.black, 
        black_i: data.black_i,
        white: data.white, 
        white_i: data.white_i,
        newPoiner: data.new
      });
    }
    // 根据新棋子
    // if (data.new) {
    //   let data_ = data.new
    //   this.origin.props.addChess(data_[2], [data_[0], data_[1]]);
    // }
    // 帮助
    if (data.help) {
      this.origin.props.showWarnig({
        showWarningForChess: true,
        axisForWarn: [data.help[0], data.help[1]],
        axisType: 0
      })
    }
    
    // 确认下子
    if (data.touch === 'button' && data.key === 'ok') {
      if (this.gameStates === 'pause') {
        this.gameStates = null
        this.sendMessage(this.ws, {
            cmd: 'key',
            key: 'resume',
            touch: 'kb_play'
        })
        this.showKbType = 'kb_play';
        this.player && this.origin.props.showTitle({
          bol: true,
          title: this.player
        })
      } else if (this.gameStates === 'help') {
        this.gameStates = null
        this.sendMessage(this.ws, {
          cmd: 'key',
          key: 'help',
          touch: 'kb_play'
        })
        this.showKbType = 'kb_play';
        this.helpTime++;
        this.player && this.origin.props.showTitle({
          bol: true,
          title: this.player
        })
      } else {
        this.sendMessage(this.ws, {
          cmd: 'key',
          key: 'ok'
        })
      }
      
    }
    //state字段
    if (data.msg === 'state') {
      switch (data.state) {
        case 'gameover':
          this._countWin(data);
          break;
        case 'hp_0':
          this.origin.props.showTitle({
            bol: true,
            title: 3
          })
          this.player = 3;
          break;
        case 'hp_1':
          this.origin.props.showTitle({
            bol: true,
            title: 4
          })
          this.player = 4;
          break;
        case 'ai_0':
          this.origin.props.showTitle({
            bol: true,
            title: 5
          })
          this.player = 5;
        break;
        case 'ai_1':
          this.origin.props.showTitle({
            bol: true,
            title: 6
          })
          this.player = 6;
          break;
        default:
          break;
      }
    }
    // ptouch字段/yesno操作板
    if (data.touch === 'ptouch') {
      if (data.key === 'ok') {
        if (this.gameStates === 'help') {
          this.player && this.origin.props.showTitle({
            bol: true,
            title: this.player
          })
          this.showKbType = 'kb_play';
          this.helpTime++;
          this.sendMessage(this.ws, {
            cmd: 'key',
            key: 'help',
            touch: 'kb_play'
          })
          return;
        }
      } else if (data.key === 'cancel'){
        this.player && this.origin.props.showTitle({
          bol: true,
          title: this.player
        })
        this.sendMessage(this.ws, {
          touch: 'kb_play'
        })
      }
      if (data.key === 'quit') {
        this.sendMessage(this.ws, {
          touch: 'kb_yesno'
        })
        this.origin.props.showDialog({
          bol: true,
          dialogContent2: '棋局未下完，请确认是否结束',
        })
        this.origin.props.swithProcss('endgame');
      } else if (data.key === 'help') {
        if (this.chessType == 2 && this.helpTime === 0) {
          this.sendMessage(this.ws, {
            touch: 'kb_yesno'
          })
          this.showKbType = 'kb_yesno';
          this.gameStates = 'help';
          this.origin.props.showDialog({
            bol: true,
            dialogHeader: '提示',
            dialogContent2: '每局仅可以使用一次，请确认是否提示',
          })
        }
      } else if (data.key === 'pause') {
        this.sendMessage(this.ws, {
          cmd: 'key',
          key: 'pause',
          touch: 'kb_none'
        })
        this.showKbType = '';
        this.gameStates = 'pause';
        this.origin.props.showDialog({
          bol: true,
          dialogHeader: '暂停',
          dialogContent1: '棋局暂停，请点击',
          dialogContent2: '确认键继续',
        })
      }
    }
  }
  // 手动结束游戏部分
  _handleEndGame(data) {
    if (data.msg === 'state') {
      if (data.state === 'gameover') {
        this._countWin(data);
      }
    }
    if (data.key === 'ok') {
      if (this.chessType === 1) {
        this.sendMessage(this.ws, {
          cmd: 'key',
          key: 'referee'
        })
        this.origin.props.showDialog({
          bol: true,
          dialogContent2: '正在计算输赢',
        })
      } else if (this.chessType === 2) {
        this.resetGame()
      }
    } else if (data.key === 'cancel') {
      //返回play进程
      this.origin.props.swithProcss('play');
      // 恢复弹框
      this.player && this.origin.props.showTitle({
        bol: true,
        title: this.player
      })
      //恢复操控版
      this.sendMessage(this.ws, {
        touch: 'kb_play'
      })
    }
  }
  // 计算胜负部分
  _handleWinner(data) {
    if (data.key === 'ok') {
      this.resetGame();
    }
  }
}

export default createWS;