import React, {Component} from 'react';
import {Route, Redirect, withRouter} from "react-router-dom";
import GameOptionsTitle from '../GameOptionTitle';
import Options from '../GameOptions';
import Play from '../GamePlay';

class Game extends Component {

  render() {
    
    return(
      <div>
        <GameOptionsTitle/>
         <Route path={`${this.props.match.url}/`} exact render={()=> (
               <Redirect to={'/options'}/>
           )}/>
        <Route path={`${this.props.match.url}/options`} component={Options}/>
        <Route path={`${this.props.match.url}/play`} component={Play}/>
      </div>
    )
  }
}

export default withRouter(Game);