import React from 'react';
import './style.scss';
import none from '../../static/img/voice/none.png';
import jia from '../../static/img/voice/jia.png';
import laba from '../../static/img/voice/laba.png';
//import lv from '../../static/img/voice/lv.png';

function Voice(props) {
  let dom;
  let classes = `vol-${props.voice.toString()}`;
  if (props.voice === 0) {
    dom = <img src={none} alt=""/>
  } else {
    dom = 
      <div className="voice-n-wrap">
        <img src={laba} alt=""/>
        <div className="voice-n-lv-wrap">
          <div className={classes + ' voice-n-lv'}></div>
        </div>
        <img src={jia} alt=""/>
      </div>
  }
  return(
    <div>
      {dom}
    </div>
  )
}
export default Voice;