const initialState = {
  dialogType: 1,
  dialog: false,
  winner:0,
  dialogHeader:'',
  dialogContent1:'',
  dialogContent2:'',
};

// action types
export const types = {
  SHOW_DIALOG:'DIALOG/SHOW_DIALOG',
  SHOW_TITLE:'DIALOG/SHOW_TITLE',
};

// action creators
export const actions = {
  showDialog: (data) => ({
    type: types.SHOW_DIALOG,
    data
  }),
  showTitle: (data) => ({
    type: types.SHOW_TITLE,
    data
  })
};

// reducers
const reducer = (state = initialState, action) => {
  switch (action.type) {
    case types.SHOW_DIALOG:

      if (action.data.bol) {
        return { 
          ...state, 
          dialog: true,
          dialogType: 1,
          dialogHeader: action.data.dialogHeader,
          dialogContent1: action.data.dialogContent1,
          dialogContent2: action.data.dialogContent2,
        }
      } else {
        return { 
          ...state, 
          dialog: false,
          dialogType: null,
          dialogHeader: '',
          dialogContent1: '',
          dialogContent2: '',
        }
      }
    case types.SHOW_TITLE:
      if (action.data.bol) {
        return { 
          ...state, 
          dialog: true,
          dialogType: 2,
          dialogTitle: action.data.title
        }
      } else {
        return { 
          ...state, 
          dialog: false,
          dialogType: null,
          dialogTitle: ''
        }
      }
    default:
      return state;
  }
};

export default reducer;


