import React, { Component } from 'react';
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {getList, getStep, actions} from '../../redux/modules/game';
import {actions as appActions} from '../../redux/modules/app';
import GameItem from '../../components/GameItem';
//import GameControl from '../GameControl';

import './style.scss';

class Game extends Component {
  
  //配置棋
  deployChess = (id) => {
    console.log(id, typeof id);
    if (process.env.NODE_ENV === 'production') {
      return;
    }
    switch(this.props.steps) {
        case 0:
          this.props.setOptions('chess', id);
          break;
        case 1:
          this.props.setOptions('role', id);
          break;
        case 2:
          this.props.setOptions('level', id);
          this.props.history.push('/game/play');
          break;
        default: 
        return 
      }
    this.props.switchGameStep();
  }
  //切换选项-鼠标移入
  switchItem = (id) => {
    //console.log(id)
    //this.props.switchOptons(id);
  }
  getOptions = (step) => {
    switch(step) {
      case 0:
        return {
          classes: 'step-1 chess-layout-flex',
        }
      case 1:
        return {
          classes: 'step-2 chess-layout-flex',
        }
      case 2:
        return {
          classes: 'step-3 chess-layout-flex'
        }
      default: 
        return {
          classes: 'step-4 chess-layout-flex',
        }
    }
  }
  
  componentDidMount = () => {
    //console.log(this.props.history)
  }
  
  // componentWillUnmount = () => {
  //   console.log('game组件卸载');
  //   this.props.switchTitleImgFull(true);
  // }
  
  render() {

    let {optionList, steps, options} = this.props;
    let optionListKeys = optionList.keys();
    let {classes} = this.getOptions(steps);
    let list = [];
    let domWrap;
    console.log(optionListKeys);
    if (steps < 2) {
      optionList.forEach((item) => {
        //console.log(optionList.keys().next())
        let id = optionListKeys.next().value;
        console.log(optionListKeys)
        list.push(
          <GameItem 
            key = {item.id}
            source = {optionList}
            setID = {this.deployChess.bind(null, id)}
            setChoosed = {this.switchItem.bind(null, id)}
            imgUrl = {item.url}
            choosedImgUrl = {item.choosedImgUrl}
            choosed = {item.choosed}
          />)
      })
      domWrap = <div className = {classes}>
        {list}
      </div>
    } else {
      let list1 = [];
      let list2 = [];
      optionList.forEach((item) => {
        let id = optionListKeys.next().value;
        if (item.floor === 1) {
          list1.push(
            <GameItem 
              key = {item.id}
              source = {optionList}
              setID = {this.deployChess.bind(null, id)}
              setChoosed = {this.switchItem.bind(null, id)}
              imgUrl = {item.url}
              choosedImgUrl = {item.choosedImgUrl}
              choosed = {item.choosed}
          />)
          return
        }
        list2.push(
          <GameItem 
            key = {item.id}
            source = {optionList}
            setID = {this.deployChess.bind(null, id)}
            setChoosed = {this.switchItem.bind(null, id)}
            imgUrl = {item.url}
            choosedImgUrl = {item.choosedImgUrl}
            choosed = {item.choosed}
        />)
      })
      if (options[0]['value'] === 1) {
        classes += ' weiqi'
      } else if (options[0]['value'] === 2) {
        classes += ' wuziqi'
      }
      domWrap = <div className = {classes}>
        <div>{list1}</div>
        <div>{list2}</div>
      </div>
    }
    
    return <div className='chess-bg'>
        {/* <h1 className="chess-title">
           {titleList}
        </h1> */}
        {domWrap}
    </div>
  }
}

const mapStateToProps = (state) => {
  return {
    optionList: getList(state),
    steps: getStep(state), 
    options:state.game.options
  };
};

const mapDispatchToProps = dispatch => {
  return {
    ...bindActionCreators(actions, dispatch),
    ...bindActionCreators(appActions, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Game);